"use client"

import type React from "react"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { useToast } from "@/components/ui/use-toast"
import { BackButton } from "@/components/ui/BackButton"
import axios from "axios"

export default function TesteCriarUsuarioSimples() {
  const { toast } = useToast()
  const [loading, setLoading] = useState(false)
  const [resultado, setResultado] = useState<any>(null)
  const [error, setError] = useState<string | null>(null)

  const [formData, setFormData] = useState({
    nome: "Usuário Teste",
    username: "usuario.teste",
    email: "usuario.teste@exemplo.com",
    senha: "senha123",
    cargo: "Testador",
    perfil: "Operador",
    status: "Ativo",
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleSelectChange = (name: string, value: string) => {
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)
    setResultado(null)

    try {
      // Obter a URL base e o token
      const baseURL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8080/api"
      const token = localStorage.getItem("token")

      if (!token) {
        throw new Error("Token não encontrado. Faça login primeiro.")
      }

      // Criar usuário usando axios diretamente
      const response = await axios.post(`${baseURL}/usuarios`, formData, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
      })

      setResultado({
        status: response.status,
        data: response.data,
      })

      toast({
        title: "Sucesso!",
        description: "Usuário criado com sucesso usando método direto.",
      })
    } catch (err: any) {
      console.error("Erro ao criar usuário:", err)

      setError(
        err.response
          ? `Erro ${err.response.status}: ${JSON.stringify(err.response.data)}`
          : err.message || "Erro desconhecido",
      )

      toast({
        title: "Erro ao criar usuário",
        description: err.response?.data?.message || err.message || "Erro desconhecido",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="container py-8">
      <div className="flex items-center gap-4 mb-6">
        <BackButton />
        <h1 className="text-3xl font-bold">Teste de Criação de Usuário (Método Simples)</h1>
      </div>

      <div className="grid gap-6 md:grid-cols-2">
        <Card>
          <CardHeader>
            <CardTitle>Criar Novo Usuário</CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="nome">Nome</Label>
                <Input id="nome" name="nome" value={formData.nome} onChange={handleChange} required />
              </div>

              <div className="space-y-2">
                <Label htmlFor="username">Nome de Usuário</Label>
                <Input id="username" name="username" value={formData.username} onChange={handleChange} required />
              </div>

              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input id="email" name="email" type="email" value={formData.email} onChange={handleChange} required />
              </div>

              <div className="space-y-2">
                <Label htmlFor="senha">Senha</Label>
                <Input
                  id="senha"
                  name="senha"
                  type="password"
                  value={formData.senha}
                  onChange={handleChange}
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="cargo">Cargo</Label>
                <Input id="cargo" name="cargo" value={formData.cargo} onChange={handleChange} required />
              </div>

              <div className="space-y-2">
                <Label htmlFor="perfil">Perfil</Label>
                <Select value={formData.perfil} onValueChange={(value) => handleSelectChange("perfil", value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Selecione um perfil" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Administrador">Administrador</SelectItem>
                    <SelectItem value="Operador">Operador</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="status">Status</Label>
                <Select value={formData.status} onValueChange={(value) => handleSelectChange("status", value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Selecione um status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Ativo">Ativo</SelectItem>
                    <SelectItem value="Inativo">Inativo</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <Button type="submit" className="w-full" disabled={loading}>
                {loading ? "Criando..." : "Criar Usuário"}
              </Button>
            </form>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Resultado</CardTitle>
          </CardHeader>
          <CardContent>
            {loading && (
              <div className="flex justify-center py-8">
                <div className="animate-spin h-8 w-8 border-4 border-primary border-t-transparent rounded-full"></div>
              </div>
            )}

            {error && (
              <div className="p-4 bg-red-50 border border-red-200 rounded-md text-red-800 mb-4">
                <p className="font-semibold">Erro:</p>
                <pre className="mt-2 text-sm whitespace-pre-wrap overflow-auto max-h-[300px]">{error}</pre>
              </div>
            )}

            {resultado && (
              <div className="p-4 bg-green-50 border border-green-200 rounded-md text-green-800">
                <p className="font-semibold">Status: {resultado.status}</p>
                <p className="font-semibold mt-2">Resposta:</p>
                <pre className="mt-2 text-sm whitespace-pre-wrap overflow-auto max-h-[300px]">
                  {JSON.stringify(resultado.data, null, 2)}
                </pre>
              </div>
            )}

            {!loading && !error && !resultado && (
              <div className="text-center py-8 text-muted-foreground">
                Preencha o formulário e clique em "Criar Usuário" para ver o resultado.
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
